import {BaseElement, BaseElementAsync, MsgLabel, OSkinableInit} from "back/commons/basis";
import {JWspCreateParamsDB, JWspCreateParamsDB_DRF, JWspCreateParamsDB_DRV, JWspCreateParamsFS, JWspType, JWspTypeInst} from "lib/wsp/wsp";
import {DOMSH} from "lib/commons/xml/domsh";
import {DOM, JSX} from "lib/commons/xml/dom";
import {IReg, REG} from "lib/commons/registry";
import {ERROR} from "lib/core/errorReport";
import {WSPTYPE} from "lib/wsp/wspType";
import {IChainEnv} from "lib/wsp/chain";
import {IPendingValidity, MxFormElement, OFormElementInit} from "lib/commons/forms";
import {LOCALE} from "lib/commons/lang";

export interface OWspTypeSelectorInit extends OFormElementInit {
	reg?: IReg<IChainEnv>
	mode: "new" | "edit"
	fromWspCode?: string
	readonly?: boolean
	params?: JWspCreateParamsFS | JWspCreateParamsDB | JWspCreateParamsDB_DRF | JWspCreateParamsDB_DRV | any
}

export interface IWspTypeDefMaker {
	fillWspTypeInst(): JWspTypeInst;
}


export class WspTypeSelector extends MxFormElement(BaseElementAsync) implements IWspTypeDefMaker, IPendingValidity {

	protected config: OWspTypeSelectorInit;

	protected reg: IReg<IChainEnv>;

	protected wspMetaEditorRootElt: HTMLFieldSetElement;

	protected msgElt: MsgLabel;

	protected async _initialize(init: OWspTypeSelectorInit): Promise<any> {
		this.config = init;
		this.reg = this.findReg(init);

		this.msgElt = new MsgLabel();
		this.wspMetaEditorRootElt = <fieldset tabindex="-1"/> as HTMLFieldSetElement;

		this._attach(this.localName, init, this.msgElt, <form name="wspTypeSelector">{this.wspMetaEditorRootElt}</form>);

		this.addEventListener('input', async (ev: UIEvent) => {
			this._refreshValidity();
		});

		this._initializeForm(init);

		await this._refreshAll();
	}

	/** Form */
	formDisabledCallback(disabled: boolean) {
		this.wspMetaEditorRootElt.disabled = disabled;
	}

	protected _refreshValidity() {
		const wspTypeDef: WspTypeDef = this.wspMetaEditorRootElt.querySelector("wsptype-selector-wsptypedef[checked]") as WspTypeDef;
		if (this.required && !wspTypeDef) return this.setValidity({valueMissing: true}, "Veuillez sélectionner un modèle documentaire", this.wspMetaEditorRootElt);
		if (wspTypeDef) {
			const wspOptionDefs: NodeListOf<WspOptionDef> = wspTypeDef.querySelectorAll("wsptype-selector-wspoptiondef[checked]");
			for (const wspOptionDef of wspOptionDefs) {
				if (wspOptionDef.unknown)
					return this.setValidity({valueMissing: true}, "Veuillez désélectionner les extensions inconnues", this.wspMetaEditorRootElt);
			}
		}
		this.setValidity({}, "");
	}

	/**
	 * Fonctions de refresh UI
	 */
	protected refreshUi() {
		if (this.initStatus != "pending")
			this.reinitializeStarted(this._refreshAll());
	}

	refreshPending: Promise<Document>;

	protected async _refreshAll(): Promise<void> {
		this.addPendingValidity(this);
		this.msgElt.setStandardMsg("loading");
		try {
			// - Load du wspMetaEditor
			this.refreshPending = (this.config.mode == "new" || !this.config.fromWspCode ? WSPTYPE.getNewEditor(this.reg.env.universe.wspServer, this.config.fromWspCode, this.config.params) : WSPTYPE.getUpdateEditor(this.reg.env.universe.wspServer, this.config.fromWspCode));

			// - Clean
			this.wspMetaEditorRootElt.innerHTML = "";
			let wspMetaEditorDom: Document = await this.refreshPending
			// - affichage UI
			let hasWspDefSelected: boolean = false;
			wspMetaEditorDom.firstElementChild.childNodes.forEach((node: HTMLElement) => {
				if (node.nodeName == "wspTypeDef") {
					this.wspMetaEditorRootElt.appendChild(new WspTypeDef().initialize({node}));
					if (node.getAttribute("selected") == "true") hasWspDefSelected = true;
				} else
					ERROR.log("wspTypeEditor : unknown tag '" + node.nodeName + "'");
			});

			// - Mode "new" : on sélectionne le premier wspTypeDef si uniquement un seul
			if (this.config.mode == "new" && this.wspMetaEditorRootElt.childElementCount===1)
				(this.wspMetaEditorRootElt.firstElementChild as WspTypeDef).checked = true;

			if (!this.wspMetaEditorRootElt.hasChildNodes())
				this.msgElt.setCustomMsg("Aucun modèle disponible", "info");
			else
				this.msgElt.setStandardMsg(null);

			this.disabled = this.disabled;// refraichissement UI du statut disabled
			if (this.config.readonly !== undefined) this.readonly = this.config.readonly;
			else this.readonly = this.readonly;// refraichissement UI du statut readonly
			this._refreshValidity();
		} catch (e) {
			this.msgElt.setCustomMsg("Chargement impossible", "error");
			await ERROR.log(e);
		} finally {
			this.refreshPending = null;
		}
	}

	async computeValidity(): Promise<void> {
		await this.refreshPending;
	}

	get readonly(): boolean {
		return this.hasAttribute("readonly");
	}

	set readonly(val) {
		Array.from(this.wspMetaEditorRootElt.querySelectorAll("wsptype-selector-wsptypedef")).forEach((elt: WspTypeDef) => {
			if (val === true) elt.readonly = true;
		});
		if (val === true)
			DOM.setAttr(this, "readonly", "true");
		else
			this.removeAttribute("readonly")
	}

	/**
	 * Retourne le JWspTypeInst construit
	 */
	fillWspTypeInst(): JWspTypeInst | null {
		let wspTypeDef: WspTypeDef = this.wspMetaEditorRootElt.querySelector("wsptype-selector-wsptypedef[checked]") as WspTypeDef;
		if (wspTypeDef) {
			let wspTypeInst: JWspTypeInst = {} as JWspTypeInst;
			wspTypeDef.toWspTypeInst(wspTypeInst);
			return wspTypeInst;
		}
	}

	extractJson(parent: Dict<any>): boolean {
		if (this.name && parent[this.name]) this.refreshUi();
		return false;
	}

	fillJson(parent: any, root: any): void {
		if (this.name) parent[this.name] = this.fillWspTypeInst();
	}
}

customElements.define('wsptype-selector', WspTypeSelector);
REG.reg.registerSkin('wsptype-selector', 1, /* language=CSS */ `
	:host {
		flex: 1;
	  display: flex;
	  min-height: 0;
	  min-width: 0;
	  flex-direction: column;
	  justify-content: center;
	  text-align: center;
	  vertical-align: middle;
	  overflow: auto;
  }

  fieldset {
	  margin: 0;
	  padding: 0;
	  border: none;
  }

  fieldset:focus {
	  outline: none;
  }
`);


/**
 * Tag wspTypeDef
 */
export interface OWspTypeDefInit extends OSkinableInit {
	readonly?: boolean
	node: Element
}

class WspTypeDef extends BaseElement {

	protected config: OWspTypeDefInit;

	protected inputElt: HTMLInputElement;

	private _childrenSkinElt: HTMLDivElement;
	private _childrenGenElt: HTMLDivElement;
	private _childrenExtElt: HTMLDivElement;

	protected _initialize(init: OWspTypeDefInit) {
		this.config = init;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);

		this._initAndInstallSkin("wsptype-selector-wsptypedef", init);

		this.initInput();

		if (this.unknown) DOM.addClass(this, "unknown");
		if (this.obsolete) DOM.addClass(this, "obsolete");
		if (this.newest) DOM.addClass(this, "newest");
		let label = sr.appendChild(<label>{this.inputElt} <span>{this.titleWspDef}</span></label>);
		if (this.descWspDef)
			DOM.setAttr(label, "title", this.descWspDef);
		let children = sr.appendChild(<div class="children"/>);

		if (this.config.readonly !== undefined) this.readonly = this.config.readonly;
		this.disabled = this.obsolete ? true : this.disabled;// refraichissement UI du statut disabled

		// Options filles
		this._childrenSkinElt = children.appendChild(<div>
			<div class="sublabel">Habillage des publications</div>
			<div class="optslist">
				<slot name="skin"/>
			</div>
		</div>) as HTMLDivElement;
		this._childrenGenElt = children.appendChild(<div>
			<legend class="sublabel">Publications</legend>
			<div class="optslist">
				<slot name="gen"/>
			</div>
		</div>) as HTMLDivElement;
		this._childrenExtElt = children.appendChild(<div>
			<legend class="sublabel">Extensions</legend>
			<div class="optslist">
				<slot name="ext"/>
			</div>
		</div>) as HTMLDivElement;

		if (init.node) init.node.childNodes.forEach((optionNode: HTMLElement) => {
			if (optionNode.nodeName == "wspOptionDef")
				this.appendChild(<WspOptionDef slot={optionNode.getAttribute("nature") || "ext"} î={{node: optionNode, disabled: this.disabled} as OWspTypeDefInit}/>);
		});

		this.addEventListener('input', async (ev: UIEvent) => {
			if (!this.disabled || !this.readonly) {
				const input = ev.composedPath()[0] as HTMLInputElement;
				if (input === this.inputElt)
					this.checked = input.checked;
			}
		});

		if (init.node.getAttribute("selected") === "true")
			this.checked = true;

		this._adjustUi(true)
	}

	/** Ajustemets graphiques UI en fonction des états (disabled, ...) **/
	private _adjustUi(forced?: boolean): void {
		if (forced || this.initialized) {
			let isOptionVisible = function (optionNode: WspOptionDef) {if (!optionNode.hidden) return true};
			if (Array.from(this.querySelectorAll("*[slot='skin']")).findIndex(isOptionVisible) == -1)
				DOM.setHidden(this._childrenSkinElt, true);
			if (Array.from(this.querySelectorAll("*[slot='gen']")).findIndex(isOptionVisible) == -1)
				DOM.setHidden(this._childrenGenElt, true);
			if (Array.from(this.querySelectorAll("*[slot='ext']")).findIndex(isOptionVisible) == -1)
				DOM.setHidden(this._childrenExtElt, true);
		}
	}

	get checked(): boolean {
		return this.inputElt.checked;
	}

	set checked(value: boolean) {
		this.inputElt.checked = value;
		if (value)
			DOM.setAttr(this, "checked", "true");
		else
			this.removeAttribute("checked");
		if (value && this.nodeName == "wsptype-selector-wsptypedef") {
			// on désactive les frêres wspTypeDef
			let prev: WspTypeDef = this.previousElementSibling as WspTypeDef;
			while (prev) {
				prev.checked = false;
				prev = prev.previousElementSibling as WspTypeDef;
			}
			let next: WspTypeDef = this.nextElementSibling as WspTypeDef;
			while (next) {
				next.checked = false;
				next = next.nextElementSibling as WspTypeDef;
			}
		}
		this._adjustUi();
	}

	get disabled(): boolean {
		return this.getAttribute("disabled") === "true";
	}

	set disabled(val) {
		Array.from(this.querySelectorAll("wsptype-selector-wspoptiondef")).forEach((elt: WspOptionDef) => {
			if (val === true) elt.disabled = true;
		});
		this.inputElt.disabled = val === true;
		if (val === true)
			DOM.setAttr(this, "disabled", "true");
		else
			this.removeAttribute("disabled");
		this._adjustUi();
	}

	get readonly(): boolean {
		return this.getAttribute("readonly") === "true";
	}

	set readonly(val) {
		Array.from(this.querySelectorAll("wsptype-selector-wspoptiondef")).forEach((elt: WspOptionDef) => {
			if (val === true) elt.readonly = true;
		});
		this.inputElt.disabled = val === true;
		if (val === true)
			DOM.setAttr(this, "readonly", "true");
		else
			this.removeAttribute("readonly");
		this._adjustUi();
	}

	get hidden(): boolean {
		return this.readonly && !this.checked;
	}

	/**
	 * Titre
	 */
	protected get titleWspDef(): HTMLElement {
		let details = "";
		if (this.obsolete || this.newest) details = `version ${this.config.node.getAttribute("version")}`;
		if (this.config.node.getAttribute("lang")) {
			if (details !== "") details += `, ${LOCALE.languageToName(this.config.node.getAttribute("lang"))}`;
			else details += LOCALE.languageToName(this.config.node.getAttribute("lang"));
		}
		return <span>
			<span class="title">
				{this.config.node.getAttribute("title")}
				{details ? <span> ({details})</span> : null}
			</span>
			{this.error ? <span class={"labelstatus " + this.errorLevel}>{this.error}</span> : null}
		</span>;
	}

	/**
	 * Description
	 */
	protected get descWspDef(): string {
		return this.config.node.getAttribute("version");
	}

	/**
	 * Modèle inconnu (ie pas de pack)
	 */
	get unknown(): boolean {
		return this.config.node.getAttribute("unknown") === "true";
	}

	/**
	 * Modèle obsolète (ie pack potentiellement présent, mais inutilisable. Ex : v mineure -1 avec migration)
	 */
	get obsolete(): boolean {
		return this.config.node.getAttribute("obsolete") === "true";
	}

	/**
	 * Nouveau modèle cible conseillée en cas de présence d'obsolète sélectionnée
	 */
	get newest(): boolean {
		return this.config.node.getAttribute("newest") === "true";
	}

	/**
	 * null si pas d'erreur
	 */
	protected get error(): string | null {
		if (this.obsolete)
			return "modèle obsolète";
		if (this.unknown)
			return "modèle inconnu";
		if (this.config.node.hasAttribute("@errorPack")) {
			switch (this.config.node.getAttribute("@errorPack")) {
			case "unknownError":
			case "malformedPack":
				return "modèle en erreur";
			case "versionPackUnknown":
			case "versionFrameworkPackOutdated":
				return "modèle trop ancien, mise à jour nécessaire";
			case "versionServerOutdated":
				return "modèle trop récent, incompatible avec cet entrepôt";
			default:
				return "modèle en erreur";
			}
		}
	}

	protected get errorLevel(): "warning" | "error" | null {
		if (this.unknown || this.config.node.hasAttribute("@errorPack"))
			return 'error';
		if (this.obsolete)
			return 'warning'
		return null;
	}

	/**
	 * Construit le tag input
	 */
	protected initInput(): HTMLInputElement {
		this.inputElt = <input type="radio" name="wspTypeDef" value={this.config.node.getAttribute("uri")} form="wspTypeSelector"/> as HTMLInputElement;
		return this.inputElt;
	}

	/**
	 * Retourne un élément JWspTypeInst
	 */
	public toWspTypeInst(wspTypeInst: JWspTypeInst): void {
		if (this.checked && !this.obsolete) {
			wspTypeInst.wspType = {
				key: this.config.node.getAttribute("key"),
				lang: this.config.node.getAttribute("lang"),
				version: this.config.node.getAttribute("version"),
				title: this.config.node.getAttribute("title"),
				uri: this.config.node.getAttribute("uri"),
			} as JWspType;

			Array.from(this.querySelectorAll("*")).forEach((node: WspOptionDef) => {
				node.toWspTypeInst(wspTypeInst);
			})
		}
	}

}

customElements.define('wsptype-selector-wsptypedef', WspTypeDef);
