/**
 * Tag wspOptionDef
 */

class WspOptionDef extends WspTypeDef {

	protected initInput(): HTMLInputElement {
		this.inputElt = <input type="checkbox" name="wspTypeDef" value={this.config.node.getAttribute("uri")} form="wspTypeSelector"/> as HTMLInputElement;
		return this.inputElt;
	}

	public toWspTypeInst(wspTypeInst: JWspTypeInst): void {
		if (this.checked && !this.obsolete) {
			if (!wspTypeInst.wspOptions)
				wspTypeInst.wspOptions = [];
			wspTypeInst.wspOptions.push({
				key: this.config.node.getAttribute("key"),
				lang: this.config.node.getAttribute("lang"),
				version: this.config.node.getAttribute("version"),
				title: this.config.node.getAttribute("title"),
				uri: this.config.node.getAttribute("uri"),
			} as JWspType);
		}
	}

	/**
	 * null si pas d'erreur
	 */
	protected get error(): string | null {
		if (this.obsolete)
			return "extension obsolète";
		if (this.unknown)
			return "extension inconnue";
		if (this.config.node.hasAttribute("@errorPack")) {
			switch (this.config.node.getAttribute("@errorPack")) {
			case "unknownError":
			case "malformedPack":
				return "extension en erreur";
			case "versionPackUnknown":
			case "versionFrameworkPackOutdated":
				return "extension trop ancienne, mise à jour nécessaire";
			case "versionServerOutdated":
				return "extension trop récente, incompatible avec cet entrepôt";
			default:
				return "extension en erreur";
			}
		}
	}
}

customElements.define('wsptype-selector-wspoptiondef', WspOptionDef);